import { build } from './builder'

const urlPrefix = '/hand_break'
const polyfill = {
  test: (v, search) => v.name.toLowerCase().includes(search) ||
    (v.handBreakWeight + '').toLowerCase().includes(search),
  uniqProp: 'handBreakId'
}

const {
  search: searchHandBreaks,
  get: getHandBreaks,
  post: postHandBreak,
  put: putHandBreak,
  delete: deleteHandBreak
} = build(urlPrefix, polyfill)

export { searchHandBreaks, getHandBreaks, postHandBreak, putHandBreak, deleteHandBreak }
