import { build } from './builder'

const urlPrefix = '/serie'
const polyfill = {
  test: (v, search) => v.airBreak.name.toLowerCase().includes(search) ||
    v.handBreak.name.toLowerCase().includes(search) ||
    v.name.toLowerCase().includes(search),
  uniqProp: 'serieId'
}

const {
  search: searchSeries,
  get: getSeries,
  post: postSerie,
  put: putSerie,
  delete: deleteSerie
} = build(urlPrefix, polyfill)

export { searchSeries, getSeries, postSerie, putSerie, deleteSerie }
