import { build } from './builder'

const urlPrefix = '/owner'
const polyfill = {
  test: (v, search) => v.name.toLowerCase().includes(search) ||
    v.email.toLowerCase().includes(search) ||
    v.telephone.toLowerCase().includes(search) ||
    v.iCO.toLowerCase().includes(search),
  uniqProp: 'subjectId'
}

const {
  search: searchOwners,
  get: getOwners,
  post: postOwner,
  put: putOwner,
  delete: deleteOwner
} = build(urlPrefix, polyfill)

export { searchOwners, getOwners, postOwner, putOwner, deleteOwner }
