import { reCSharpKeys, download } from './common'

function build (urlPrefix, polyfill) {
  return {
    async get (skip, count) {
      const { data } = await download({
        method: 'get',
        url: `${urlPrefix}/${skip}/${count}`
      })
      return data
    },

    async post (item) {
      const { data } = await download({
        method: 'post',
        url: `${urlPrefix}`,
        headers: {
          'content-type': 'application/json'
        },
        data: reCSharpKeys(item)
      })
      return data
    },

    async put (item) {
      return download({
        method: 'put',
        url: `${urlPrefix}`,
        headers: {
          'content-type': 'application/json'
        },
        data: reCSharpKeys(item)
      })
    },

    async delete (id) {
      return download({
        method: 'delete',
        url: `${urlPrefix}/${id}`
      })
    },

    // @todo: there should be an API for this
    async search (search, skip, count) {
      search = search.toLowerCase()
      const { data } = await download({
        method: 'get',
        url: `${urlPrefix}/0/100`
      })
      const set = new Set()
      return data
        .filter(v => {
          try {
            if (
              v != null &&
              !set.has(v[polyfill.uniqProp]) &&
              polyfill.test(v, search)
            ) {
              set.add(v[polyfill.uniqProp])
              return true
            } else {
              return false
            }
          } catch (_) {
            return false
          }
        })
        .filter((_, i) => i >= skip && i < skip + count)
    }
  }
}

export { build }
