<template>
  <v-layout row wrap>
    <v-flex xs6>
      <v-menu
        :close-on-content-click="false"
        v-model="menus.date"
        lazy
        transition="scale-transition"
        offset-y
        full-width
      >
        <v-text-field
          slot="activator"
          v-model="date"
          :label="label"
          readonly
        />
        <v-date-picker
          v-model="date"
          type="date"
        >
          <v-spacer/>
          <v-btn flat v-text="$t('close')" @click="menus.date = false"/>
        </v-date-picker>
      </v-menu>
    </v-flex>
    <v-flex xs6>
      <v-menu
        :close-on-content-click="false"
        v-model="menus.time"
        lazy
        transition="scale-transition"
        offset-y
        full-width
      >
        <v-text-field
          slot="activator"
          v-model="time"
          :label="$t('time')"
          readonly
        />
        <v-time-picker
          v-model="time"
          format="24hr"
        >
          <v-spacer/>
          <v-btn flat v-text="$t('close')" @click="menus.time = false"/>
        </v-time-picker>
      </v-menu>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  props: {
    value: String,
    label: String
  },
  data: () => ({
    date: null,
    time: null,
    seconds: null,
    menus: { date: false, time: false }
  }),
  computed: {
    item: {
      get () {
        const d = this.date != null && this.date.length === 10
          ? this.date
          : '1970-01-01'
        const t = this.time != null && this.time.length === 5
          ? this.time
          : '00:00'
        const s = this.seconds != null && this.seconds.length === 2
          ? this.seconds
          : '00'

        return `${d}T${t}:${s}`
      },
      set (val) {
        if (val == null || val.length < 19) {
          this.date = null
          this.time = null
          this.seconds = null
        } else {
          this.date = val.slice(0, 10)
          this.time = val.slice(11, 16)
          this.seconds = val.slice(17, 19)
        }
      }
    }
  },
  watch: {
    value (val) {
      this.item = val
    },
    item (val) {
      if (this.date || this.time || this.seconds) {
        this.$emit('input', val)
      }
    }
  },
  mounted () {
    this.item = this.value
  }
}
</script>
