<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" app>
      <v-list>
        <v-list-tile v-for="({text, icon, to}, i) in items" :key="i" :to="to">
          <v-list-tile-action>
            <v-icon>mdi-checkbox-blank</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title v-text="text"/>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar app extension-height="7px">
      <v-toolbar-side-icon @click.stop="drawer = !drawer"/>

      <v-toolbar-title class="headline text-uppercase">
        <span class="accent--text">KST</span>
        <span class="primary--text font-weight-light">INPRO</span>
      </v-toolbar-title>

      <v-spacer/>

      <v-progress-linear
        v-show="progress.show"
        :indeterminate="progress.indeterminate === true"
        :value="progress.value"
        class="ma-0"
        slot="extension"
        color="accent"
      />
      <v-alert v-model="alert.show" :type="alert.type" dismissible class="mt-0" transition="fade-transition" slot="extension" style="position: absolute; width: 100%; top: 7px;">
        {{ alert.text }}
      </v-alert>
    </v-toolbar>

    <v-content>
      <transition name="fade" mode="out-in">
        <router-view/>
      </transition>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    drawer: true,
    rawItems: [
      { text: 'home', icon: 'mdi-home', to: { name: 'home' } },
      { text: 'administration', icon: 'mdi-account', to: { name: 'administration' } },
      { text: 'owners', icon: 'mdi-domain', to: { name: 'owners' } },
      { text: 'cars', icon: 'mdi-train', to: { name: 'cars' } },
      { text: 'series', icon: 'mdi-train', to: { name: 'series' } },
      { text: 'air-breaks', icon: 'mdi-stop', to: { name: 'air_breaks' } },
      { text: 'hand-breaks', icon: 'mdi-stop', to: { name: 'hand_breaks' } },
      { text: 'login', icon: 'mdi-key-variant', to: { name: 'login' } },
      { text: 'logout', icon: 'mdi-key-variant', to: { name: 'logout' } }
    ]
  }),
  computed: {
    allowedRouteNames () {
      return this.$store.getters.allowedRouteNames
    },
    items () {
      return this.rawItems.filter(item =>
        this.allowedRouteNames.includes(item.to.name)
      ).map(item => ({
        ...item,
        text: this.$t(item.text)
      }))
    },
    progress () {
      const working = this.$store.state.working
      return {
        show: working !== false,
        indeterminate: working === true,
        value: typeof working === 'number' ? working : 0
      }
    },
    alert () {
      return this.$store.state.alert
    }
  }
}
</script>

<style scoped>
.v-list__tile--link:not(.v-list__tile--active) > .v-list__tile__action {
  visibility: hidden;
}
</style>

