<template>
  <DialogWrapper :headline="$t('user')" :value="show" @save="save" @cancel="cancel">
    <v-flex xs12 md6>
      <v-text-field :label="$t('first-name')" v-model="item.firstName" required/>
    </v-flex>
    <v-flex xs12 md6>
      <v-text-field :label="$t('last-name')" v-model="item.lastName" required/>
    </v-flex>
    <v-flex xs12>
      <v-text-field :label="$t('email')" v-model="item.email" required/>
    </v-flex>
    <v-flex xs12>
      <v-text-field :label="$t('password')" v-model="item.password" type="password" required/>
    </v-flex>
    <v-flex xs12>
      <v-select :label="$t('role')" :items="roles" v-model="item.role.roleId" required/>
    </v-flex>
  </DialogWrapper>
</template>

<script>
import DialogMixin from './DialogMixin'

export default {
  mixins: [ DialogMixin ],
  data: () => ({
    item: {
      role: {}
    }
  }),
  computed: {
    roles () {
      return [1, 2, 3, 4].map(roleId => ({
        text: this.$t(`roles.${roleId}`),
        value: roleId
      }))
    }
  },
  methods: {
    save () {
      // @todo
      this.item.role.name = this.$t(`roles.${this.item.role.roleId}`)
      DialogMixin.methods.save.call(this)
    }
  }
}
</script>
