import { build } from './builder'

const urlPrefix = '/air_break'
const polyfill = {
  test: (v, search) => v.name.toLowerCase().includes(search) ||
    (v.airBreakWeight + '').toLowerCase().includes(search),
  uniqProp: 'airBreakId'
}

const {
  search: searchAirBreaks,
  get: getAirBreaks,
  post: postAirBreak,
  put: putAirBreak,
  delete: deleteAirBreak
} = build(urlPrefix, polyfill)

export { searchAirBreaks, getAirBreaks, postAirBreak, putAirBreak, deleteAirBreak }
