import { build } from './builder'

const urlPrefix = '/user'
const polyfill = {
  test: (v, search) => v.firstName.toLowerCase().includes(search) ||
    v.lastName.toLowerCase().includes(search) ||
    v.email.toLowerCase().includes(search) ||
    v.role.name.toLowerCase().includes(search),
  uniqProp: 'userId'
}

const {
  search: searchUsers,
  get: getUsers,
  post: postUser,
  put: putUser,
  delete: deleteUser
} = build(urlPrefix, polyfill)

export { searchUsers, getUsers, postUser, putUser, deleteUser }
