import Router from 'vue-router'
import Vue from 'vue'
import store from './store'

import About from './views/About.vue'
import Administration from './views/Administration.vue'
import AirBreaks from './views/AirBreaks.vue'
import Cars from './views/Cars.vue'
import HandBreaks from './views/HandBreaks.vue'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Logout from './views/Logout.vue'
import Owners from './views/Owners.vue'
import Series from './views/Series.vue'

Vue.use(Router)

const router = new Router({
  routes: [ {
    path: '/',
    redirect: '/login'
  }, {
    path: '/login/:next?',
    name: 'login',
    component: Login
  }, {
    path: '/logout',
    name: 'logout',
    component: Logout
  }, {
    path: '/home',
    name: 'home',
    component: Home
  }, {
    path: '/cars',
    name: 'cars',
    component: Cars
  }, {
    path: '/administration',
    name: 'administration',
    component: Administration
  }, {
    path: '/owners',
    name: 'owners',
    component: Owners
  }, {
    path: '/air_breaks',
    name: 'air_breaks',
    component: AirBreaks
  }, {
    path: '/hand_breaks',
    name: 'hand_breaks',
    component: HandBreaks
  }, {
    path: '/series',
    name: 'series',
    component: Series
  }, {
    path: '/about',
    name: 'about',
    component: About
    // // route level code-splitting
    // // this generates a separate chunk (about.[hash].js) for this route
    // // which is lazy-loaded when the route is visited.
    // component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
  } ]
})

router.beforeEach(({ name, path }, _from, next) => {
  store.commit('clearAlert')
  if (store.getters.allowedRouteNames.includes(name)) {
    next()
  } else {
    next({ name: 'login', params: { next: path } })
  }
})

export default router
