import { build } from './builder'

const urlPrefix = '/car'
const polyfill = {
  test: (v, search) => v.name.toLowerCase().includes(search) ||
    v.owner.name.toLowerCase().includes(search) ||
    v.serviceResponsiblePerson.name.toLowerCase().includes(search) ||
    v.serie.name.toLowerCase().includes(search),
  uniqProp: 'carId'
}

const {
  search: searchCars,
  get: getCars,
  post: postCar,
  put: putCar,
  delete: deleteCar
} = build(urlPrefix, polyfill)

export { searchCars, getCars, postCar, putCar, deleteCar }
